<template>
  <div class="page-animate" :class="{'page-animate-active':pageAnimate}">
    <span class="last-total-title">剩餘總額細項</span>
    <div class="row justify-content-center">
      <div class="col-md-10">
        <div class="last-total-outer-frame">
          <div class="last-total-outer" :class="{'last-total-outer-active':lastTotal}">
            <div class="row no-gutters">
              <div class="col-md-6">
                <div class="show-get-group">
                  <span class="get-total-title">收入總計細項</span>
                  <span>本月收入 {{currentGetMonthCount}} 元</span>
                  <span>本年收入 {{currentGetYearCount}} 元</span>
                  <span>本月最高收入 {{currentGetMonthHeightCount}} 元</span>
                  <span>本年最高收入 {{currentGetYearHeightCount}} 元</span>
                </div>
              </div>
              <div class="col-md-6">
                <div class="show-cost-group">
                  <span class="cost-total-title">支出總計細項</span>
                  <span>本月支出 {{currentCostMonthCount}} 元</span>
                  <span>本年支出 {{currentCostYearCount}} 元</span>
                  <span>本月最高支出 {{currentCostMonthHeightCount}} 元</span>
                  <span>本年最高支出 {{currentCostYearHeightCount}} 元</span>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="show-last">
                  <span>本月剩餘 {{currentGetMonthCount-currentCostMonthCount}} 元</span>
                  <span>本年剩餘 {{currentGetYearCount-currentCostYearCount}} 元</span>
                  <span>統計時間截至 {{currentDate}}</span>
                </div>
              </div>
            </div>
            <div class="row justify-content-center">
              <div class="col-md-5">
                <div class="back">
                  <span @click="goBack">返回功能選項</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 1s ease;
  .last-total-title {
    display: block;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    padding: 20px 0 20px 0;
    color: white;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .last-total-outer-frame {
    overflow: hidden;
    .last-total-outer {
      margin-top: -700px;
      transition: 1s ease;
      .show-get-group,
      .show-cost-group {
        color: white;
        text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
        background-color: rgba(0, 0, 0, 0.7);
        padding: 10px;
        margin: 5px;
        border-radius: 10px;
        box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7),
          0 0 2px 1px rgba(0, 0, 0, 0.7);
      }
      .show-last {
        font-weight: bold;
        font-size: 20px;
        margin: 5px;
        color: rgb(200, 200, 200);
      }
      .show-get-group,
      .show-cost-group,
      .show-last {
        span {
          display: block;
          line-height: 2em;
          text-align: center;
          color: white;
          text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
        }
        .get-total-title,
        .cost-total-title {
          font-size: 20px;
          font-weight: bold;
        }
      }
      .back {
        cursor: pointer;
        user-select: none;
        margin: 15px 0 15px 0;
        background-color: rgb(0, 102, 255);
        border-radius: 20px;
        span {
          text-align: center;
          color: white;
          display: block;
          padding: 5px 0 5px 0;
        }
      }
    }
    .last-total-outer-active {
      margin-top: 0;
    }
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      currentGetMonthTemp: [],
      currentGetMonth: [],
      currentGetMonthNum: [],
      currentGetMonthCount: null,
      currentGetMonthHeightCount: null,
      currentGetYearNum: [],
      currentGetYearCount: null,
      currentGetYearHeightCount: null,
      currentCostMonthTemp: [],
      currentCostMonth: [],
      currentCostMonthNum: [],
      currentCostMonthCount: null,
      currentCostMonthHeightCount: null,
      currentCostYearNum: [],
      currentCostYearCount: null,
      currentCostYearHeightCount: null,
      currentDate: null,
      pageAnimate: false,
      lastTotal: false,
    };
  },
  mounted() {
    setTimeout(() => (this.pageAnimate = true), 100);
    setTimeout(() => (this.lastTotal = true), 1100);
  },
  methods: {
    goBack() {
      this.lastTotal = false;
      setTimeout(() => (this.pageAnimate = false), 1000);
      setTimeout(() => this.$router.push("/main"), 2000);
    },
    timeObject() {
      const time = new Date();
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      let monthT = `${time.getMonth() + 1 < 10 ? "0" : ""}${
        time.getMonth() + 1
      }`;
      let date = `${time.getDate() + 1 < 10 ? "0" : ""}${time.getDate()}`;
      this.currentDate = `${year} / ${monthT} / ${date}`;
      this.getCost(year, month);
      this.getGets(year, month);
    },
    getCostMonthPart(month) {
      this.currentCostMonthTemp.forEach((key) => {
        if (key.costMonth == month) {
          this.currentCostMonth.push(key);
          this.currentCostMonthHeightCount = Math.max(
            ...this.currentCostMonth.map((key) => key.costCash)
          );
        }
      });
      this.currentCostMonth.forEach((key) =>
        this.currentCostMonthNum.push(Number(key.costCash))
      );
      this.currentCostMonthCount = this.currentCostMonthNum.reduce(
        (front, end) => front + end
      );
    },
    getCostYearPart() {
      this.currentCostMonthTemp.forEach((key) =>
        this.currentCostYearNum.push(Number(key.costCash))
      );
      this.currentCostYearHeightCount = Math.max(
        ...this.currentCostMonthTemp.map((key) => key.costCash)
      );
      this.currentCostYearCount = this.currentCostYearNum.reduce(
        (front, end) => front + end
      );
    },
    getCost(year, month) {
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/cost/0/year/${year}/month.json`
        )
        .then((res) => {
          this.currentCostMonthTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.getCostMonthPart(month);
          this.getCostYearPart();
        })
        .catch((err) => console.log(err));
    },
    getGetsMonthPart(month) {
      this.currentGetMonthTemp.forEach((key) => {
        if (key.getMonth == month) {
          this.currentGetMonth.push(key);
          this.currentGetMonthHeightCount = Math.max(
            ...this.currentGetMonth.map((key) => key.getCash)
          );
        }
      });
      this.currentGetMonth.forEach((key) =>
        this.currentGetMonthNum.push(Number(key.getCash))
      );
      this.currentGetMonthCount = this.currentGetMonthNum.reduce(
        (front, end) => front + end
      );
    },
    getGetsYearPart() {
      this.currentGetMonthTemp.forEach((key) =>
        this.currentGetYearNum.push(Number(key.getCash))
      );
      this.currentGetYearHeightCount = Math.max(
        ...this.currentGetMonthTemp.map((key) => key.getCash)
      );
      this.currentGetYearCount = this.currentGetYearNum.reduce(
        (front, end) => front + end
      );
    },
    getGets(year, month) {
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/gets/0/year/${year}/month.json`
        )
        .then((res) => {
          this.currentGetMonthTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.getGetsMonthPart(month);
          this.getGetsYearPart();
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.timeObject();
  },
};
</script>