<template>
  <div class="page-animate" :class="{'page-animate-active':pageAnimate}">
    <span class="get-year-title">{{currentYear}} 年每月收入</span>
    <div class="row justify-content-center">
      <div class="col-md-10">
        <div class="get-year-choose-outer-frame">
          <div class="get-year-choose-outer" :class="{'get-year-choose-outer-active':chooseGet}">
            <div class="get-year-choose">
              <label for="get-year">年份</label>
              <select id="get-year" v-model="currentYear" @change="timeObject(hand=true)">
                <option
                  v-for="(years,index) in 6"
                  :key="index"
                  :value="Number(`202${index}`)"
                >{{`202${index}`}}</option>
              </select>
            </div>
            <ve-histogram
              :data="chartData"
              :colors="colors"
              :extend="extend"
              :settings="chartSettings"
            ></ve-histogram>
            <div class="row justify-content-center">
              <div class="col-md-5">
                <div class="back">
                  <span @click="goBack">返回功能選項</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 1s ease;
  .get-year-title {
    display: block;
    text-align: center;
    font-size: 30px;
    padding: 20px 0 20px 0;
    color: white;
    font-weight: bold;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .get-year-choose-outer-frame {
    overflow: hidden;
    .get-year-choose-outer {
      padding: 5px 0 5px 0;
      margin-top: -700px;
      transition: 1s ease;
      .get-year-choose {
        display: flex;
        justify-content: flex-end;
        label {
          display: block;
          margin: -3px 5px -5px 5px;
          font-size: 20px;
          color: white;
          font-weight: bold;
          text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
        }
        select {
          cursor: pointer;
          user-select: none;
          -webkit-appearance: none;
          padding: 0 10px 0 10px;
          border-radius: 20px;
          outline: none;
          border: none;
          background-color: rgb(255, 255, 255);
          box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7),
            0 0 2px 1px rgba(0, 0, 0, 0.7);
          transition: 0.7s ease;
        }
        select:focus {
          box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7),
            0 0 2px 1px rgba(0, 0, 0, 0.7);
        }
      }
      .ve-histogram {
        margin: 25px 0 25px 0;
        height: 365px !important;
        padding: 10px;
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 20px;
        box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.7);
        overflow: hidden;
      }
      .back {
        cursor: pointer;
        user-select: none;
        margin: 15px 0 15px 0;
        background-color: rgb(0, 102, 255);
        border-radius: 20px;
        span {
          text-align: center;
          color: white;
          display: block;
          padding: 5px 0 5px 0;
        }
      }
    }
    .get-year-choose-outer-active {
      margin-top: 0;
    }
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
import "v-charts/lib/style.css";
export default {
  data() {
    (this.colors = ["rgb(0, 17, 255)"]),
      (this.extend = {
        series: {
          label: {
            show: true,
            position: "top",
          },
        },
      }),
      (this.chartSettings = {
        labelMap: {
          month: "月份",
          get: "收入金額 ( 元 )",
        },
      });
    return {
      chartData: {
        columns: ["month", "get"],
        rows: [],
      },
      loading: false,
      yearItem: [
        {
          yearNum: 0,
          year: 2020,
        },
        {
          yearNum: 1,
          year: 2021,
        },
        {
          yearNum: 2,
          year: 2022,
        },
        {
          yearNum: 3,
          year: 2023,
        },
        {
          yearNum: 4,
          year: 2024,
        },
        {
          yearNum: 5,
          year: 2025,
        },
      ],
      yearNumTemp: null,
      allGetGroupTemp: [],
      allGetGroup: [],
      currentYear: null,
      pageAnimate: false,
      chooseGet: false,
    };
  },
  mounted() {
    setTimeout(() => (this.pageAnimate = true), 100);
    setTimeout(() => (this.chooseGet = true), 1000);
  },
  methods: {
    goBack() {
      this.chooseGet = false;
      setTimeout(() => (this.pageAnimate = false), 1000);
      setTimeout(() => this.$router.push("/main"), 2000);
    },
    timeObject(hand) {
      const time = new Date();
      let year = time.getFullYear();
      if (hand == undefined) {
        this.currentYear = year;
        this.yearItem.forEach((key) =>
          key.year == year ? (this.yearNumTemp = key.yearNum) : null
        );
        this.getAllGets();
      } else {
        this.yearItem.forEach((key) =>
          key.year == this.currentYear ? (this.yearNumTemp = key.yearNum) : null
        );
        this.allGetGroup = [];
        this.chartData.rows = [];
        this.getAllGets();
      }
    },
    getAllGets() {
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/getsTotal/0/year/${this.currentYear}/month.json`
        )
        .then((res) => {
          this.allGetGroupTemp = res.data;
          this.allGetGroupTemp.forEach((key) =>
            key !== null ? this.allGetGroup.push(key) : null
          );
          this.allGetGroup = this.allGetGroup.sort(
            (max, min) => max.getDate - min.getDate
          );
          this.allGetGroup.forEach((key) =>
            this.chartData.rows.push({
              month: `${key.month} 月`,
              get: key.total,
            })
          );
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.timeObject();
  },
};
</script>