<template>
  <div class="page-animate" :class="{'page-animate-active':pageAnimate}">
    <span class="add-title-cost">新增支出項目</span>
    <div class="row justify-content-center">
      <div class="col-md-10">
        <div class="cost-column-outer-frame">
          <div class="cost-column-outer" :class="{'cost-column-outer-active':costColumn}">
            <div class="cost-column">
              <div class="row">
                <div class="col-md-6">
                  <div class="date-input-group">
                    <span>支出日期</span>
                    <div class="date-choose">
                      <select class="choose-year" v-model="year">
                        <option :value="null" disabled>請選擇年</option>
                        <option
                          v-for="(years,index) in 6"
                          :key="index"
                          :value="Number(`202${index}`)"
                        >{{`202${index}`}} 年</option>
                      </select>
                      <select class="choose-month" v-model="month">
                        <option :value="null" disabled>請選擇月</option>
                        <option
                          v-for="(months,index) in 12"
                          :key="index"
                          :value="months"
                        >{{months}} 月</option>
                      </select>
                      <select class="choose-date" v-model="date">
                        <option :value="null" disabled>請選擇日</option>
                        <option v-for="(dates,index) in 31" :key="index" :value="dates">{{dates}} 日</option>
                      </select>
                    </div>
                  </div>
                  <div class="cost-input-group">
                    <div class="cost-name-outer">
                      <label for="cost-name">支出名稱</label>
                      <input
                        type="text"
                        id="cost-name"
                        v-model="costName"
                        placeholder="-- 請輸入支出名稱 --"
                      />
                    </div>
                    <div class="cost-cash-outer">
                      <label for="cost-cash">支出金額</label>
                      <input
                        type="tel"
                        id="cost-cash"
                        v-model="costCash"
                        placeholder="-- 請輸入支出金額 --"
                      />
                    </div>
                    <div class="cost-type-outer">
                      <label for="cost-type">支出項目</label>
                      <select id="cost-type" v-model="costType">
                        <option :value="null" disabled>-- 請選擇項目 --</option>
                        <option
                          v-for="(items,index) in costItem"
                          :key="index"
                          :value="items.names"
                        >{{items.names}}</option>
                      </select>
                    </div>
                    <div class="cost-way-outer">
                      <label for="cost-way">支付類別</label>
                      <select id="cost-way" v-model="costWay">
                        <option :value="null" disabled>-- 請選擇類別 --</option>
                        <option
                          v-for="(items,index) in costWayItem"
                          :key="index"
                          :value="items.ways"
                        >{{items.ways}}</option>
                      </select>
                    </div>
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="cost-details">
                    <span
                      class="cost-date-show"
                    >日期：{{year == null?'--':year}} / {{10 > month && month !== null?'0':''}}{{month == null?'--':month}} / {{10 > date && date !== null?'0':''}}{{date == null?'--':date}}</span>
                    <span>名稱：{{costName == ''?'無':costName}}</span>
                    <span>金額：{{costCash == ''?'無':`${costCash} 元`}}</span>
                    <span>項目：{{costType == null?'無':costType}}</span>
                    <span>類別：{{costWay == null?'無':costWay}}</span>
                    <div class="send-outer" @click="postCost">
                      <div class="send">送出</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row justify-content-center">
              <div class="col-md-5">
                <div class="back">
                  <span @click="goBack">返回功能選項</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modalC">
      <div class="modal-bodyC">
        <div class="modal-headerC">
          <span>提示訊息</span>
        </div>
        <div class="modal-contentC">
          <p>
            {{modalText}}
            <br />
            {{modalCount}} 秒後將關閉視窗
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 0.7s ease;
  .add-title-cost {
    display: block;
    text-align: center;
    font-size: 30px;
    padding: 20px 0 20px 0;
    color: white;
    font-weight: bold;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .cost-column-outer-frame {
    overflow: hidden;
    .cost-column-outer {
      margin-top: -700px;
      transition: 1s ease;
      .cost-column {
        color: white;
        background-color: rgba(0, 0, 0, 0.7);
        padding: 15px;
        border-radius: 10px;
        box-shadow: inset 0 0 3px 1px rgba(255, 255, 255, 0.7),
          0 0 3px 1px rgba(0, 0, 0, 0.7);
        .date-input-group {
          display: flex;
          justify-content: center;
          flex-direction: column;
          span {
            display: block;
            margin: 0 0 3px 0;
          }
          .date-choose {
            display: flex;
            select {
              -webkit-appearance: none;
              width: 100%;
              padding: 3px 0 1px 12px;
              margin: 2px 0 2px 0;
              outline: none;
              border: none;
              background-color: rgb(255, 255, 255);
              box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7);
              transition: 0.7s ease;
            }
            select:focus {
              box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7);
            }
            .choose-year {
              border-radius: 20px 0 0 20px;
            }
            .choose-month {
              border-radius: 0 0 0 0;
            }
            .choose-date {
              border-radius: 0 20px 20px 0;
            }
          }
        }
        .cost-input-group {
          display: flex;
          justify-content: center;
          flex-direction: column;
          label {
            display: block;
            margin: 5px 0 5px;
          }
          input[type="text"],
          input[type="tel"] {
            width: 100%;
            border-radius: 20px;
            padding: 2px 0 2px 0;
            outline: none;
            border: none;
            text-align: center;
            box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
            transition: 0.7s ease;
          }
          input[type="text"]:focus,
          input[type="tel"]:focus {
            box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
          }
          select {
            -webkit-appearance: none;
            width: 100%;
            padding: 3px 0 1px 12px;
            border-radius: 20px;
            outline: none;
            border: none;
            background-color: rgb(255, 255, 255);
            box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
            transition: 0.7s ease;
          }
          select:focus {
            box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
          }
        }
        .cost-details {
          span {
            display: block;
            margin: 10px 0 10px 0;
          }
          .send-outer {
            background-color: rgb(0, 110, 255);
            border-radius: 10px;
            padding: 42px;
            margin: auto 2px;
            cursor: pointer;
            user-select: none;
            .send {
              text-align: center;
              font-size: 28px;
            }
          }
          @media screen and (max-width: 414px) {
            .send-outer {
              padding: 3px 0 0 0;
            }
          }
        }
      }
      .back {
        cursor: pointer;
        user-select: none;
        margin: 15px 0 15px 0;
        background-color: rgb(0, 102, 255);
        border-radius: 20px;
        span {
          text-align: center;
          color: white;
          display: block;
          padding: 5px 0 5px 0;
        }
      }
    }
    .cost-column-outer-active {
      margin-top: 0;
    }
  }
  .modalC {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    .modal-bodyC {
      position: absolute;
      top: 50%;
      left: 50%;
      border: 1px solid black;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
      background: white;
      border-radius: 10px;
      margin: 2% auto;
      min-width: 380px;
      opacity: 0;
      transform: translate(-50%, -50%) scale(0.1);
      transition: 0.5s ease;
      .modal-headerC {
        padding: 8px 10px 8px 10px;
        border-bottom: 1px solid black;
        span {
          display: block;
          font-size: 20px;
          font-weight: bold;
        }
      }
      .modal-contentC {
        padding: 15px;
        text-align: center;
        p {
          margin-bottom: 0;
        }
      }
    }
    .modal-body-toggleC {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
      transition: 0.5s ease;
    }
  }
  .modal-toggleC {
    opacity: 1;
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      cashGroup: [],
      year: null,
      month: null,
      date: null,
      costName: "",
      costCash: "",
      costType: null,
      costWay: null,
      costWayItem: [
        {
          ways: "現金",
        },
        {
          ways: "信用卡",
        },
        {
          ways: "悠遊卡",
        },
      ],
      costItem: [
        {
          names: "飲食",
        },
        {
          names: "衣物",
        },
        {
          names: "生活",
        },
        {
          names: "交通費",
        },
        {
          names: "娛樂",
        },
      ],
      yearItem: [
        {
          yearNum: 0,
          year: 2020,
        },
        {
          yearNum: 1,
          year: 2021,
        },
        {
          yearNum: 2,
          year: 2022,
        },
        {
          yearNum: 3,
          year: 2023,
        },
        {
          yearNum: 4,
          year: 2024,
        },
        {
          yearNum: 5,
          year: 2025,
        },
      ],
      yearNumTemp: null,
      currentTime: "",
      rwdStatus: false,
      currentTotal: null,
      pageAnimate: false,
      costColumn: false,
      modalText: "",
      modalCount: 3,
      checkAllColumn: false,
    };
  },
  mounted() {
    setTimeout(() => (this.pageAnimate = true), 100);
    setTimeout(() => (this.costColumn = true), 1100);
  },
  methods: {
    showModal() {
      const modalShow = document.querySelector(".modalC");
      const modalContentShow = document.querySelector(".modal-bodyC");
      modalShow.style.display = "block";
      modalShow.style.transition = "1s ease";
      setTimeout(() => {
        modalShow.classList.add("modal-toggleC");
        modalContentShow.classList.add("modal-body-toggleC");
      }, 100);
      let counter = setInterval(() => {
        if (this.modalCount == 0) {
          clearInterval(counter);
          this.closeModal(modalShow, modalContentShow);
          setTimeout(() => (this.modalCount = 3), 1000);
        } else {
          this.modalCount -= 1;
        }
      }, 1000);
    },
    closeModal(modalShow, modalContentShow) {
      modalShow.classList.remove("modal-toggleC");
      modalContentShow.classList.remove("modal-body-toggleC");
      setTimeout(() => (modalShow.style.display = ""), 750);
    },
    goBack() {
      this.costColumn = false;
      setTimeout(() => (this.pageAnimate = false), 1000);
      setTimeout(() => this.$router.push("/main"), 2000);
    },
    timeObject() {
      const time = new Date();
      let hour = `${time.getHours() < 10 ? "0" : ""}${time.getHours()}`;
      let minute = `${time.getMinutes() < 10 ? "0" : ""}${time.getMinutes()}`;
      let secondes = `${time.getSeconds() < 10 ? "0" : ""}${time.getSeconds()}`;
      this.currentTime = `${hour}：${minute}：${secondes}`;
    },
    postCheck() {
      this.year == null ||
      this.month == null ||
      this.date == null ||
      this.costName == "" ||
      this.costCash == "" ||
      this.costType == null ||
      this.costWay == null
        ? (this.checkAllColumn = false)
        : (this.checkAllColumn = true);
    },
    postCost() {
      this.postCheck();
      if (this.checkAllColumn == false) {
        this.modalText = "其餘欄位不可為空";
        this.showModal();
        return;
      }
      this.timeObject();
      this.yearItem.forEach((key) =>
        key.year == this.year ? (this.yearNumTemp = key.yearNum) : null
      );
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/costTotal/0/year/${this.year}/month/${this.month}.json`
        )
        .then((res) => {
          this.currentTotal = Number(res.data.total);
          this.axios.put(
            `https://my-cash-20xx.firebaseio.com/cashdb/costTotal/0/year/${this.year}/month/${this.month}.json`,
            {
              month: this.month,
              total: this.currentTotal + Number(this.costCash),
            }
          );
        })
        .catch((err) => console.log(err));
      this.axios
        .post(
          `https://my-cash-20xx.firebaseio.com/cashdb/cost/${this.yearNumTemp}/year/${this.year}/month.json`,
          {
            costDate: this.date,
            costMonth: this.month,
            costFullDate: `${this.year} / ${this.month < 10 ? "0" : ""}${
              this.month
            } / ${this.date < 10 ? "0" : ""}${this.date}`,
            costFullTime: this.currentTime,
            costName: this.costName,
            costCash: this.costCash,
            costType: this.costType,
            costWay: this.costWay,
          }
        )
        .then((res) => {
          if (res.status == 200) {
            this.modalText = "新增支出成功";
            this.year = null;
            this.month = null;
            this.date = null;
            this.costType = null;
            this.costName = "";
            this.costCash = "";
            this.costWay = "";
            this.currentTime = "";
            this.showModal();
          } else {
            this.modalText = "新增支出失敗";
            this.year = null;
            this.month = null;
            this.date = null;
            this.costType = null;
            this.costName = "";
            this.costCash = "";
            this.costWay = "";
            this.currentTime = "";
            this.showModal();
          }
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>