<template>
  <div>
    <div class="header">
      <div class="login-off-outer">
        <div class="login-off" :class="{'login-off-active':loginStatus}" @click="loginOff">登出</div>
      </div>
      <h1>收支管理系統</h1>
    </div>
    <div class="main">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-10">
            <span class="time-show">{{timeShow}}</span>
          </div>
        </div>
        <div class="row justify-content-center" v-if="loginStatus == false">
          <div class="col-md-10">
            <div class="login-outer-frame" :class="{'login-outer-frame-active':loginInput}">
              <div class="login-outer">
                <div class="login">
                  <div class="login-header">登入系統</div>
                  <div class="login-body">
                    <label for="account">帳號</label>
                    <input id="account" type="text" placeholder="-- 請輸入使用者帳號 --" v-model="account" />
                    <label for="password">密碼</label>
                    <input
                      id="password"
                      type="password"
                      placeholder="-- 請輸入使用者密碼 --"
                      v-model="password"
                    />
                  </div>
                  <div class="login-footer" @click="login">登入</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div v-else class="content" :class="{'content-hide':contentStatus}">
          <router-view />
        </div>
      </div>
    </div>
    <div class="footer">
      <h6>
        <i class="fal fa-copyright"></i> CopyRight Chen
      </h6>
    </div>
    <div class="modalL">
      <div class="modal-bodyL">
        <div class="modal-headerL">
          <span>提示訊息</span>
        </div>
        <div class="modal-contentL">
          <p>
            {{modalTextF}}
            <br />
            {{modalCount}} 秒後將關閉視窗{{modalTextE}}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.header {
  overflow: hidden;
  .login-off-outer {
    display: flex;
    justify-content: flex-end;
    .login-off {
      font-size: 18px;
      margin: -2px -2px 0 0;
      padding: 5px 10px 3px 10px;
      background-color: rgba(0, 0, 0, 0.7);
      color: white;
      border-radius: 0 0 0 10px;
      box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7);
      transition: 1s ease;
      transform: translateY(-300px);
      cursor: pointer;
      user-select: none;
    }
    .login-off-active {
      transform: translateY(0px);
    }
  }
  h1 {
    padding: 0 20px 0 20px;
    font-weight: bold;
    color: white;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  @media screen and (max-width: 768px) {
    h1 {
      padding: 20px 0 20px 0;
      text-align: center;
    }
  }
}
.main {
  .time-show {
    display: block;
    text-align: right;
    margin: 16px 0 16px 0;
    font-weight: bold;
    color: white;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
  }
  @media screen and (max-width: 768px) {
    .time-show {
      font-size: 18px;
    }
  }
  .login-outer-frame {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    min-height: 50vh;
    opacity: 0;
    transition: 1s ease;
    .login-outer {
      border-radius: 10px;
      box-shadow: inset 0 0 3px 2px rgba(255, 255, 255, 0.9),
        0 0 2px 1px rgba(0, 0, 0, 0.7);
      .login {
        min-width: 380px;
        color: white;
        .login-header {
          font-size: 18px;
          font-weight: bold;
          padding: 10px 0 10px 10px;
          background-color: rgba(0, 0, 0, 0.7);
          border-bottom: 1px solid rgba(255, 255, 255, 0.7);
          border-radius: 10px 10px 0 0;
        }
        .login-body {
          padding: 10px 20px 10px 20px;
          background-color: rgba(0, 0, 0, 0.7);
          label {
            display: block;
            margin: 5px 0 5px 0;
          }
          input[type="text"],
          input[type="password"] {
            width: 100%;
            margin: 5px 0 5px 0;
            text-align: center;
            outline: none;
            border: none;
            border-radius: 20px;
            box-shadow: inset 0 0 0 0 rgba(0, 110, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
            transition: 0.7s ease;
          }
          input[type="text"]:focus,
          input[type="password"]:focus {
            box-shadow: inset 0 0 3px 1px rgba(0, 110, 255, 0.7),
              0 0 2px 1px rgba(0, 0, 0, 0.7);
          }
        }
        .login-footer {
          text-align: center;
          padding: 10px 0 10px 0;
          background-color: rgba(0, 0, 0, 0.7);
          border-top: 1px solid rgba(255, 255, 255, 0.7);
          border-radius: 0 0 10px 10px;
        }
      }
    }
  }
  .login-outer-frame-active {
    opacity: 1;
  }
  .content {
    opacity: 1;
    transition: 1s ease;
  }
  .content-hide {
    opacity: 0;
  }
}
.footer {
  color: white;
  text-align: center;
  h6 {
    font-size: 25px;
    padding: 40px 0 40px 0;
    font-style: italic;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
    margin-bottom: 0;
  }
}
.modalL {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: rgba(0, 0, 0, 0.7);
  .modal-bodyL {
    position: absolute;
    top: 50%;
    left: 50%;
    border: 1px solid black;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
    background: white;
    border-radius: 10px;
    margin: 2% auto;
    min-width: 380px;
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.1);
    transition: 0.5s ease;
    .modal-headerL {
      padding: 8px 10px 8px 10px;
      border-bottom: 1px solid black;
      span {
        display: block;
        font-size: 20px;
        font-weight: bold;
      }
    }
    .modal-contentL {
      padding: 15px;
      text-align: center;
      p {
        margin-bottom: 0;
      }
    }
  }
  .modal-body-toggleL {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
    transition: 0.5s ease;
  }
}
.modal-toggleL {
  opacity: 1;
}
</style>
<script>
import md5 from "md5";
export default {
  data() {
    return {
      account: "",
      password: "",
      AcPs: [],
      loginStatus: false,
      contentStatus: false,
      timeShow: "",
      dayItem: [
        {
          num: 0,
          dayName: "周日",
        },
        {
          num: 1,
          dayName: "周一",
        },
        {
          num: 2,
          dayName: "周二",
        },
        {
          num: 3,
          dayName: "周三",
        },
        {
          num: 4,
          dayName: "周四",
        },
        {
          num: 5,
          dayName: "周五",
        },
        {
          num: 6,
          dayName: "周六",
        },
      ],
      loginInput: false,
      modalCount: 3,
      modalTextF: "",
      modalTextE: "",
    };
  },
  mounted() {
    setTimeout(() => (this.loginInput = true), 100);
  },
  methods: {
    showModal() {
      const modalShow = document.querySelector(".modalL");
      const modalContentShow = document.querySelector(".modal-bodyL");
      modalShow.style.display = "block";
      modalShow.style.transition = "1s ease";
      setTimeout(() => {
        modalShow.classList.add("modal-toggleL");
        modalContentShow.classList.add("modal-body-toggleL");
      }, 100);
      let counter = setInterval(() => {
        if (this.modalCount == 0) {
          setTimeout(() => (this.modalCount = 3), 1000);
          clearInterval(counter);
          this.closeModal(modalShow, modalContentShow);
          this.modalTextF == "登出成功"
            ? this.loginOffPart()
            : this.loginPart();
        } else {
          this.modalCount -= 1;
        }
      }, 1000);
    },
    closeModal(modalShow, modalContentShow) {
      modalShow.classList.remove("modal-toggleL");
      modalContentShow.classList.remove("modal-body-toggleL");
      setTimeout(() => (modalShow.style.display = ""), 750);
    },
    loginPart() {
      if (this.modalTextF == "登入成功") {
        this.loginInput = false;
        this.contentStatus = false;
        this.account = "";
        this.password = "";
        document.cookie = `L_S=${md5("success")};path=/`;
        setTimeout(() => {
          this.$router.push("/main").catch((err) => {
            err;
          });
          this.loginStatus = true;
        }, 1000);
      } else {
        this.loginStatus = false;
        this.$router.push("/").catch((err) => {
          err;
        });
        this.account = "";
        this.password = "";
      }
    },
    loginOffPart() {
      this.contentStatus = true;
      setTimeout(() => {
        this.loginStatus = false;
        document.cookie = `L_S='';expires=Thu, 01 Jan 1970 00:00:00 GMT';path=/;`;
        this.$router.push("/");
      }, 1000);
      setTimeout(() => (this.loginInput = true), 1050);
    },
    loginOff() {
      this.modalTextF = "登出成功";
      this.modalTextE = "跳轉回登入畫面";
      this.showModal();
    },
    getTime() {
      setInterval(() => {
        const time = new Date();
        let day = "";
        let year = time.getFullYear();
        let month = `${time.getMonth() + 1 < 10 ? "0" : ""}${
          time.getMonth() + 1
        }`;
        let date = `${time.getDate() < 10 ? "0" : ""}${time.getDate()}`;
        let hour = `${time.getHours() < 10 ? "0" : ""}${time.getHours()}`;
        let minute = `${time.getMinutes() < 10 ? "0" : ""}${time.getMinutes()}`;
        let secodes = `${
          time.getSeconds() < 10 ? "0" : ""
        }${time.getSeconds()}`;
        this.dayItem.forEach((key) =>
          key.num == time.getDay() ? (day = key.dayName) : null
        );
        this.timeShow = `${hour}:${minute}:${secodes} ${day} ${year} / ${month} / ${date}`;
      }, 1000);
    },
    getAccount() {
      this.axios
        .get("https://acwithps-20xx.firebaseio.com/cash.json")
        .then((res) => this.AcPs.push(res.data))
        .catch((err) => console.log(err));
    },
    login() {
      this.AcPs.forEach((key) => {
        if (key.account == this.account && key.password == this.password) {
          this.modalTextF = "登入成功";
          this.modalTextE = "跳轉管理介面";
          this.showModal();
        } else {
          this.modalTextF = "帳密錯誤或欄位不得為空";
          this.modalTextE = "";
          this.showModal();
        }
      });
    },
  },
  created() {
    this.getTime();
    this.getAccount();
    document.cookie == `L_S=${md5("success")}`
      ? (this.loginStatus = true)
      : (this.loginStatus = false);
  },
};
</script>