import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import Main from '../components/Main.vue'
import AddCost from '../components/DashChild/AddCost.vue'
import AddGet from '../components/DashChild/AddGet.vue'
import CurrentCostMonth from '../components/DashChild/CurrentCostMonth.vue'
import EveryCostMonth from '../components/DashChild/EveryCostMonth.vue'
import CurrentGetMonth from '../components/DashChild/CurrentGetMonth.vue'
import EveryGetMonth from '../components/DashChild/EveryGetMonth.vue'
import LastTotal from '../components/DashChild/LastTotal.vue'
import CostTest from '../components/DashChild/CostTest.vue'
Vue.use(VueRouter)

const routes = [{
  path: '*',
  redirect: '/'
},
{
  path: '/',
  name: 'login',
  component: Login,
  children: [{
    path: 'main',
    name: 'main',
    component: Main,
    meta: {
      requiresAuth: true
    },
  }, {
    path: 'add_cost',
    name: 'addcost',
    component: AddCost,
    meta: {
      requiresAuth: true
    },
  }, {
    path: 'add_get',
    name: 'addget',
    component: AddGet,
    meta: {
      requiresAuth: true
    },
  },
  {
    path: 'current_cost_month',
    name: 'current_cost_month',
    component: CurrentCostMonth,
    meta: {
      requiresAuth: true
    },
  }, {
    path: 'every_cost_month',
    name: 'every_cost_month',
    component: EveryCostMonth,
    meta: {
      requiresAuth: true
    },
  },
  {
    path: 'current_get_month',
    name: 'current_get_month',
    component: CurrentGetMonth,
    meta: {
      requiresAuth: true
    },
  }, {
    path: 'every_get_month',
    name: 'every_get_month',
    component: EveryGetMonth,
    meta: {
      requiresAuth: true
    },
  }, {
    path: 'last_total',
    name: 'last_total',
    component: LastTotal,
    meta: {
      requiresAuth: true
    },
  }, {
    path: 'cost_test',
    name: 'cost_test',
    component: CostTest,
    meta: {
      requiresAuth: true
    },
  }]
}
]

const router = new VueRouter({
  routes
})

export default router
