<template>
  <div class="page-animate" :class="{'page-animate-active':pageAnimate}">
    <span class="test-count-title">支出試算</span>
    <div class="row justify-content-center">
      <div class="col-md-5">
        <div class="test-count-outer-frame">
          <div class="test-count-outer" :class="{'test-count-outer-active':testCount}">
            <div class="test-count">
              <div class="test-count-header">
                <div class="get-num-show">
                  <div class="get-for-month">
                    <span>本月餘額</span>
                    <span>本年餘額</span>
                  </div>
                  <div class="get-for-year">
                    <span>{{getMonthCount-costMonthCount}} 元</span>
                    <span>{{getYearCount-costYearCount}} 元</span>
                  </div>
                </div>
                <div class="prepped-for-pay">
                  <span>預計支出</span>
                  <span>{{showNum == ""?'等待輸入中':`${showNum} 元`}}</span>
                </div>
                <div class="final-count">
                  <span class="final-count-title">計算結果</span>
                  <div class="final-count-month">
                    <span>本月支出後餘額</span>
                    <span>{{finalMonthCount == ''?'等待計算中':`${finalMonthCount} 元`}}</span>
                  </div>
                  <div class="final-count-year">
                    <span>本年支出後餘額</span>
                    <span>{{finalYearCount == ''?'等待計算中':`${finalYearCount} 元`}}</span>
                  </div>
                </div>
              </div>
              <div class="test-count-body">
                <span class="show-count-title" @click="showCalculator">
                  {{showCal == false?showCalText = '開起計算機':showCalText = '關閉計算機'}}
                  <i
                    class="fal fa-angle-up arrow"
                    :class="{'arrow-active':showCal}"
                  ></i>
                </span>
                <div class="test-count-outer">
                  <div class="test-count" :class="{'test-count-active':showCal}">
                    <div class="num-btn-group">
                      <div
                        v-for="(num,index) in 9"
                        :key="index"
                        :data-num="num"
                        class="num-btn num"
                        @click="numCounter"
                      >{{num}}</div>
                      <div class="num-btn-zero num" data-num="0" @click="numCounter">0</div>
                    </div>
                    <div class="num-count-group">
                      <div class="back-btn" @click="deleteNum">
                        <i class="fas fa-angle-left"></i>
                      </div>
                      <div class="clear-btn" @click="deleteAll">C</div>
                      <div class="count-btn" @click="countCounter">計算</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row justify-content-center">
              <div class="col-md-5">
                <div class="back">
                  <span @click="goBack">返回功能選項</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 1s ease;
  .test-count-title {
    display: block;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    padding: 20px 0 20px 0;
    color: white;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .test-count-outer-frame {
    overflow: hidden;
    .test-count-outer {
      margin-top: -700px;
      transition: 1s ease;
      .test-count {
        .test-count-header {
          .get-num-show {
            display: flex;
            justify-content: space-between;
            .get-for-month,
            .get-for-year {
              span {
                display: block;
                text-align: center;
                color: white;
                font-weight: bold;
                font-size: 20px;
                text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
              }
            }
          }
          .prepped-for-pay {
            display: flex;
            justify-content: space-between;
            span {
              display: block;
              text-align: center;
              color: white;
              font-weight: bold;
              font-size: 20px;
              text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
            }
          }
          .final-count {
            margin: 20px 0 20px 0;
            .final-count-title {
              display: block;
              color: white;
              font-weight: bold;
              font-size: 20px;
              text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
            }
            .final-count-month {
              display: flex;
              justify-content: space-between;
              span {
                display: block;
                text-align: center;
                color: white;
                font-weight: bold;
                font-size: 20px;
                text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
              }
            }
            .final-count-year {
              display: flex;
              justify-content: space-between;
              span {
                display: block;
                text-align: center;
                color: white;
                font-weight: bold;
                font-size: 20px;
                text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
              }
            }
          }
        }
        .test-count-body {
          .show-count-title {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            color: white;
            font-weight: bold;
            font-size: 20px;
            text-shadow: 0 1px 1px rgba(0, 0, 0, 7);
            cursor: pointer;
            user-select: none;
            .arrow {
              padding: 5px;
              font-size: 25px;
              transform: rotate(0deg);
              transition: 0.7s ease;
            }
            .arrow-active {
              transform: rotate(180deg);
            }
          }
          .test-count-outer {
            overflow: hidden;
            margin: 10px 0 5px 0;
            .test-count {
              display: flex;
              justify-content: center;
              margin-top: -100%;
              transition: 0.7s ease;
              .num-btn-group {
                display: flex;
                flex-wrap: wrap;
                width: 264px;
                .num-btn {
                  width: 86px;
                  height: 86px;
                  display: flex;
                  justify-content: center;
                  flex-direction: column;
                  align-items: center;
                  color: white;
                  background-color: rgba(0, 0, 0, 0.9);
                  font-size: 25px;
                  margin: 1px;
                  border-radius: 8px;
                  box-shadow: inset 0 0 3px 1px rgba(255, 255, 255, 0.6);
                  cursor: pointer;
                  user-select: none;
                  transition: 0.3s ease;
                }
                .num-btn-zero {
                  width: 262px;
                  height: 86px;
                  font-size: 25px;
                  display: flex;
                  justify-content: center;
                  flex-direction: column;
                  align-items: center;
                  background-color: rgba(0, 0, 0, 0.9);
                  color: white;
                  margin: 1px;
                  border-radius: 8px;
                  box-shadow: inset 0 0 3px 1px rgba(255, 255, 255, 0.6);
                  cursor: pointer;
                  user-select: none;
                  transition: 0.3s ease;
                }
                .num-btn-active {
                  color: black;
                  background-color: rgba(255, 255, 255, 0.9);
                  box-shadow: inset 0 0 3px 1px rgba(0, 0, 0, 0.6);
                }
              }
              .num-count-group {
                .back-btn,
                .clear-btn,
                .count-btn {
                  width: 86px;
                  height: 86px;
                  display: flex;
                  justify-content: center;
                  flex-direction: column;
                  align-items: center;
                  color: white;
                  font-size: 25px;
                  background-color: rgba(0, 0, 0, 0.9);
                  margin: 2px 1px 2px 1px;
                  border-radius: 8px;
                  box-shadow: inset 0 0 3px 1px rgba(255, 255, 255, 0.6);
                  cursor: pointer;
                  user-select: none;
                  transition: 0.3s ease;
                }
                .back-btn {
                  margin: 1px 1px 2px 1px;
                }
                .count-btn {
                  height: 174px;
                  margin: 2px 1px 2px 1px;
                }
                .num-btn-active {
                  color: black;
                  background-color: rgba(255, 255, 255, 0.9);
                  box-shadow: inset 0 0 3px 1px rgba(0, 0, 0, 0.6);
                }
              }
            }
            .test-count-active {
              margin-top: 0;
            }
          }
        }
      }
      .back {
        cursor: pointer;
        user-select: none;
        margin: 15px 0 15px 0;
        background-color: rgb(0, 102, 255);
        border-radius: 20px;
        span {
          text-align: center;
          color: white;
          display: block;
          padding: 5px 0 5px 0;
        }
      }
    }
    .test-count-outer-active {
      margin-top: 0;
    }
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      getMonthTemp: [],
      getMonth: [],
      getMonthNum: [],
      getMonthCount: null,
      getYear: [],
      getYearNum: [],
      getYearCount: null,
      costMonthTemp: [],
      costMonth: [],
      costMonthNum: [],
      costMonthCount: null,
      costYear: [],
      costYearNum: [],
      costYearCount: null,
      numInput: [],
      showNum: "",
      countNum: 1,
      finalMonthCount: "",
      finalYearCount: "",
      showCal: false,
      showCalText: "",
      pageAnimate: false,
      testCount: false,
    };
  },
  mounted() {
    setTimeout(() => (this.pageAnimate = true), 100);
    setTimeout(() => (this.testCount = true), 1100);
  },
  methods: {
    goBack() {
      this.testCount = false;
      setTimeout(() => (this.pageAnimate = false), 1000);
      setTimeout(() => this.$router.push("/main"), 2000);
    },
    showCalculator() {
      this.showCal == false ? (this.showCal = true) : (this.showCal = false);
    },
    numCounter(e) {
      this.finalMonthCount = "";
      this.finalYearCount = "";
      this.numInput.push(e.target.dataset.num);
      this.showNum = this.numInput.join("");
      document.querySelectorAll(".num").forEach((key) => {
        key.dataset.num == e.target.dataset.num
          ? key.classList.add("num-btn-active")
          : key.classList.remove("num-btn-active");
        setTimeout(() => key.classList.remove("num-btn-active"), 300);
      });
    },
    countCounter() {
      document.querySelector(".count-btn").classList.add("num-btn-active");
      setTimeout(
        () =>
          document
            .querySelector(".count-btn")
            .classList.remove("num-btn-active"),
        300
      );
      this.finalMonthCount =
        this.getMonthCount - this.costMonthCount - Number(this.showNum);
      this.finalYearCount =
        this.getYearCount - this.costYearCount - Number(this.showNum);
    },
    deleteNum() {
      document.querySelector(".back-btn").classList.add("num-btn-active");
      setTimeout(
        () =>
          document
            .querySelector(".back-btn")
            .classList.remove("num-btn-active"),
        300
      );
      this.finalMonthCount = "";
      this.finalYearCount = "";
      this.numInput.splice(Number(this.numInput.length - 1), 1);
      this.showNum = this.numInput.join("");
    },
    deleteAll() {
      document.querySelector(".clear-btn").classList.add("num-btn-active");
      setTimeout(
        () =>
          document
            .querySelector(".clear-btn")
            .classList.remove("num-btn-active"),
        300
      );
      this.numInput = [];
      this.finalMonthCount = "";
      this.finalYearCount = "";
      this.showNum = this.numInput.join("");
    },
    timeObject() {
      const time = new Date();
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      this.getGetsYM(year, month);
      this.getCostYM(year, month);
    },
    getCostMonth(month) {
      this.costMonthTemp.forEach((key) =>
        key.costMonth == month ? this.costMonth.push(key) : null
      );
      this.costMonth.forEach((key) =>
        this.costMonthNum.push(Number(key.costCash))
      );
      this.costMonthCount = this.costMonthNum.reduce(
        (front, end) => front + end
      );
    },
    getCostYear() {
      this.costMonthTemp.forEach((key) =>
        this.costYearNum.push(Number(key.costCash))
      );
      this.costYearCount = this.costYearNum.reduce((front, end) => front + end);
    },
    getGetsMonth(month) {
      this.getMonthTemp.forEach((key) =>
        key.getMonth == month ? this.getMonth.push(key) : null
      );
      this.getMonth.forEach((key) =>
        this.getMonthNum.push(Number(key.getCash))
      );
      this.getMonthCount = this.getMonthNum.reduce((front, end) => front + end);
    },
    getGetsYear() {
      this.getMonthTemp.forEach((key) =>
        this.getYearNum.push(Number(key.getCash))
      );
      this.getYearCount = this.getYearNum.reduce((front, end) => front + end);
    },
    getGetsYM(year, month) {
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/gets/0/year/${year}/month.json`
        )
        .then((res) => {
          this.getMonthTemp = Object.keys(res.data).map((key) => res.data[key]);
          this.getGetsMonth(month);
          this.getGetsYear();
        })
        .catch((err) => console.log(err));
    },
    getCostYM(year, month) {
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/cost/0/year/${year}/month.json`
        )
        .then((res) => {
          this.costMonthTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.getCostMonth(month);
          this.getCostYear();
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.timeObject();
  },
};
</script>