<template>
  <div class="page-animate" :class="{'page-animate-active':pageAnimate}">
    <span class="add-title-get">新增收入項目</span>
    <div class="get-cash-group-outer-frame">
      <div class="get-cash-group-outer" :class="{'get-cash-group-outer-active':getCashGroup}">
        <div class="row justify-content-center">
          <div class="col-md-10">
            <div class="get-cash-group">
              <div class="row">
                <div class="col-md-6">
                  <div class="get-date-outer">
                    <div class="get-date">
                      <span>收入日期</span>
                      <div class="choose-date">
                        <select class="choose-year" v-model="year">
                          <option :value="null" disabled>請選擇年</option>
                          <option
                            v-for="(years,index) in 6"
                            :key="index"
                            :value="Number(`202${index}`)"
                          >{{`202${index}`}} 年</option>
                        </select>
                        <select class="choose-month" v-model="month">
                          <option :value="null" disabled>請選擇月</option>
                          <option
                            v-for="(months,index) in 12"
                            :key="index"
                            :value="months"
                          >{{months}} 月</option>
                        </select>
                        <select class="choose-date" v-model="date">
                          <option :value="null" disabled>請選擇日</option>
                          <option
                            v-for="(dates,index) in 31"
                            :key="index"
                            :value="dates"
                          >{{dates}} 日</option>
                        </select>
                      </div>
                    </div>
                    <div class="get-cash-details-outer">
                      <div class="get-cash-outer">
                        <div class="get-name-outer">
                          <label for="get-name">收入名稱</label>
                          <input
                            type="text"
                            id="get-name"
                            v-model="getName"
                            placeholder="-- 請輸入收入名稱 --"
                          />
                        </div>
                        <label for="get-cash">收入金額</label>
                        <input type="tel" v-model="getCash" placeholder="-- 請輸入收入金額 --" />
                      </div>
                      <div class="get-cash-type-outer">
                        <label for="get-cash-type">收入項目</label>
                        <select id="get-cash-type" v-model="getCashType">
                          <option :value="null" disabled>-- 請選擇項目 --</option>
                          <option
                            v-for="(getType,index) in getTypeItem"
                            :key="index"
                            :value="getType.getName"
                          >{{getType.getName}}</option>
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="get-cash-details-show">
                    <span
                      class="get-cash-date-show"
                    >日期：{{year == null?'--':year}} / {{10>month && month != null?'0':''}}{{month == null?'--':month}} / {{10>date&& date != null?'0':''}}{{date == null?'--':date}}</span>
                    <span class="cost-name-show">名稱：{{getName == ''?'無':getName}}</span>
                    <span class="get-cash-show">金額：{{getCash==''?'無':`${getCash} 元`}}</span>
                    <span class="get-cash-type-show">項目：{{getCashType == null?'無':getCashType}}</span>
                    <div class="send-outer" @click="postGet">
                      <div class="send">送出</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row justify-content-center">
              <div class="col-md-5">
                <div class="back">
                  <span @click="goBack">返回功能選項</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modalG">
      <div class="modal-bodyG">
        <div class="modal-headerG">
          <span>提示訊息</span>
        </div>
        <div class="modal-contentG">
          <p>
            {{modalText}}
            <br />
            {{modalCount}} 秒後將關閉視窗
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 1s ease;
  .add-title-get {
    display: block;
    text-align: center;
    font-size: 30px;
    padding: 20px 0 20px 0;
    color: white;
    font-weight: bold;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .get-cash-group-outer-frame {
    overflow: hidden;
    padding: 5px;
    .get-cash-group-outer {
      margin-top: -700px;
      transition: 1s ease;
      .get-cash-group {
        color: white;
        background-color: rgba(0, 0, 0, 0.7);
        padding: 15px;
        border-radius: 10px;
        box-shadow: inset 0 0 3px 1px rgba(255, 255, 255, 0.7),
          0 0 3px 1px rgba(0, 0, 0, 0.7);
        .get-date-outer {
          .get-date {
            span {
              display: block;
              margin: 0 0 3px 0;
            }
            .choose-date {
              display: flex;
              select {
                -webkit-appearance: none;
                width: 100%;
                padding: 3px 0 1px 12px;
                margin: 2px 0 2px 0;
                outline: none;
                border: none;
                background-color: rgb(255, 255, 255);
                box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7);
                transition: 0.7s ease;
              }
              select:focus {
                box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7);
              }
              .choose-year {
                border-radius: 20px 0 0 20px;
              }
              .choose-month {
                border-radius: 0 0 0 0;
              }
              .choose-date {
                border-radius: 0 20px 20px 0;
              }
            }
          }
        }
        .get-cash-details-outer {
          .get-cash-outer {
            label {
              display: block;
              margin: 5px 0 5px;
            }
            input[type="tel"],
            input[type="text"] {
              width: 100%;
              border-radius: 20px;
              padding: 2px 0 2px 0;
              outline: none;
              border: none;
              text-align: center;
              box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7),
                0 0 2px 1px rgba(0, 0, 0, 0.7);
              transition: 0.7s ease;
            }
            input[type="text"]:focus,
            input[type="tel"]:focus {
              box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7),
                0 0 2px 1px rgba(0, 0, 0, 0.7);
            }
          }
          .get-cash-type-outer {
            label {
              display: block;
              margin: 5px 0 5px 0;
            }
            select {
              -webkit-appearance: none;
              width: 100%;
              padding: 3px 0 1px 12px;
              border-radius: 20px;
              outline: none;
              border: none;
              background-color: rgb(255, 255, 255);
              box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7),
                0 0 2px 1px rgba(0, 0, 0, 0.7);
              transition: 0.7s ease;
            }
            select:focus {
              box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7),
                0 0 2px 1px rgba(0, 0, 0, 0.7);
            }
          }
        }
        .get-cash-details-show {
          span {
            display: block;
            margin: 10px 0 10px 0;
          }
          .send-outer {
            background-color: rgb(0, 110, 255);
            border-radius: 10px;
            padding: 28px;
            margin: auto 2px;
            cursor: pointer;
            user-select: none;
            .send {
              text-align: center;
              font-size: 28px;
            }
          }
          @media screen and (max-width: 414px) {
            .send-outer {
              padding: 3px 0 0 0;
            }
          }
        }
      }
      .back {
        cursor: pointer;
        user-select: none;
        margin: 15px 0 15px 0;
        background-color: rgb(0, 102, 255);
        border-radius: 20px;
        span {
          text-align: center;
          color: white;
          display: block;
          padding: 5px 0 5px 0;
        }
      }
    }
    .get-cash-group-outer-active {
      margin-top: 0;
    }
  }
  .modalG {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    .modal-bodyG {
      position: absolute;
      top: 50%;
      left: 50%;
      border: 1px solid black;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
      background: white;
      border-radius: 10px;
      margin: 2% auto;
      min-width: 380px;
      opacity: 0;
      transform: translate(-50%, -50%) scale(0.1);
      transition: 0.5s ease;
      .modal-headerG {
        padding: 8px 10px 8px 10px;
        border-bottom: 1px solid black;
        span {
          display: block;
          font-size: 20px;
          font-weight: bold;
        }
      }
      .modal-contentG {
        padding: 15px;
        text-align: center;
        p {
          margin-bottom: 0;
        }
      }
    }
    .modal-body-toggleG {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
      transition: 0.5s ease;
    }
  }
  .modal-toggleG {
    opacity: 1;
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      year: null,
      month: null,
      date: null,
      getName: "",
      getCash: "",
      getCashType: null,
      getTypeItem: [
        {
          getName: "薪水",
        },
        { getName: "接案" },
        {
          getName: "發票兌獎",
        },
      ],
      yearItem: [
        {
          yearNum: 0,
          year: 2020,
        },
        {
          yearNum: 1,
          year: 2021,
        },
        {
          yearNum: 2,
          year: 2022,
        },
        {
          yearNum: 3,
          year: 2023,
        },
        {
          yearNum: 4,
          year: 2024,
        },
        {
          yearNum: 5,
          year: 2025,
        },
      ],
      yearNumTemp: null,
      currentTime: "",
      currentTotal: null,
      pageAnimate: false,
      getCashGroup: false,
      modalCount: 3,
      modalText: "",
      checkAllColumn: false,
    };
  },
  mounted() {
    setTimeout(() => (this.pageAnimate = true), 100);
    setTimeout(() => (this.getCashGroup = true), 1100);
  },
  methods: {
    showModal() {
      const modalShow = document.querySelector(".modalG");
      const modalContentShow = document.querySelector(".modal-bodyG");
      modalShow.style.display = "block";
      modalShow.style.transition = "1s ease";
      setTimeout(() => {
        modalShow.classList.add("modal-toggleG");
        modalContentShow.classList.add("modal-body-toggleG");
      }, 100);
      let counter = setInterval(() => {
        if (this.modalCount == 0) {
          clearInterval(counter);
          this.closeModal(modalShow, modalContentShow);
          setTimeout(() => (this.modalCount = 3), 1000);
        } else {
          this.modalCount -= 1;
        }
      }, 1000);
    },
    closeModal(modalShow, modalContentShow) {
      modalShow.classList.remove("modal-toggleG");
      modalContentShow.classList.remove("modal-body-toggleG");
      setTimeout(() => (modalShow.style.display = ""), 750);
    },
    goBack() {
      this.getCashGroup = false;
      setTimeout(() => (this.pageAnimate = false), 1000);
      setTimeout(() => this.$router.push("/main"), 2000);
    },
    postCheck() {
      this.year == null ||
      this.month == null ||
      this.date == null ||
      this.getName == "" ||
      this.getCash == "" ||
      this.getCashType == null
        ? (this.checkAllColumn = false)
        : (this.checkAllColumn = true);
    },
    timeObject() {
      const time = new Date();
      let hour = `${time.getHours() < 10 ? "0" : ""}${time.getHours()}`;
      let minute = `${time.getMinutes() < 10 ? "0" : ""}${time.getMinutes()}`;
      let secondes = `${time.getSeconds() < 10 ? "0" : ""}${time.getSeconds()}`;
      this.currentTime = `${hour}：${minute}：${secondes}`;
    },
    postGet() {
      this.postCheck();
      if (this.checkAllColumn == false) {
        this.modalText = "其餘欄位不可為空";
        this.showModal();
        return;
      }
      this.timeObject();
      this.yearItem.forEach((key) =>
        key.year == this.year ? (this.yearNumTemp = key.yearNum) : null
      );
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/getsTotal/0/year/${this.year}/month/${this.month}.json`
        )
        .then((res) => {
          this.currentTotal = Number(res.data.total);
          this.axios.put(
            `https://my-cash-20xx.firebaseio.com/cashdb/getsTotal/0/year/${this.year}/month/${this.month}.json`,
            {
              month: this.month,
              total: this.currentTotal + Number(this.getCash),
            }
          );
        })
        .catch((err) => console.log(err));
      this.axios
        .post(
          `https://my-cash-20xx.firebaseio.com/cashdb/gets/${this.yearNumTemp}/year/${this.year}/month.json`,
          {
            getDate: this.date,
            getMonth: this.month,
            getFullDate: `${this.year} / ${this.month < 10 ? "0" : ""}${
              this.month
            } / ${this.date < 10 ? "0" : ""}${this.date}`,
            getFullTime: this.currentTime,
            getName: this.getName,
            getCash: this.getCash,
            getType: this.getCashType,
          }
        )
        .then((res) => {
          if (res.status == 200) {
            this.modalText = "新增收入成功";
            this.year = null;
            this.month = null;
            this.date = null;
            this.getCashType = null;
            this.getName = "";
            this.getCash = "";
            this.currentTime = "";
            this.showModal();
          } else {
            this.modalText = "新增收入失敗";
            this.year = null;
            this.month = null;
            this.date = null;
            this.getCashType = null;
            this.getName = "";
            this.getCash = "";
            this.currentTime = "";
            this.showModal();
          }
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>