<template>
  <div class="page-animate" :class="{'page-animate-active':pageAnimate}">
    <span class="current-month-title">{{year}} 年 {{monthText}} 月收入清單</span>
    <div class="row justify-content-center">
      <div class="col-md-10">
        <div class="current-month-get-outer-frame">
          <div
            class="current-month-get-outer"
            :class="{'current-month-get-outer-active':currentGet}"
          >
            <div class="choose-date-outer">
              <span @click="showDate">查詢其他年月</span>
              <i class="fal fa-angle-left arrow" :class="{'arrow-active':dateToggle}"></i>
              <div class="choose-date" :class="{'choose-date-active':dateToggle}">
                <select class="choose-year" v-model="year" @change="currentGetMonth">
                  <option :value="null" disabled>請選擇年份</option>
                  <option
                    v-for="(years,index) in 6"
                    :key="index"
                    :value="Number(`202${index}`)"
                  >{{`202${index}`}} 年</option>
                </select>
                <select class="choose-month" v-model="month " @change="currentGetMonth">
                  <option :value="null" disabled>請選擇月份</option>
                  <option v-for="(month,index) in 12" :key="index" :value="month">{{month}} 月</option>
                </select>
              </div>
            </div>
            <div class="current-month-get">
              <table class="table">
                <thead>
                  <tr>
                    <th scope="col">名稱</th>
                    <th scope="col">項目</th>
                    <th scope="col">金額</th>
                    <th scope="col">日期</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(gets,index) in showGetGroup" :key="index">
                    <td>{{gets.getName}}</td>
                    <td>{{gets.getType}}</td>
                    <td :class="{'alert':gets.getCash>=1000}">{{gets.getCash}} 元</td>
                    <td>{{gets.getFullDate.slice(7,14)}}</td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td v-if="allGetCount == ''" colspan="4" class="text-center">
                      <i class="fal fa-angle-double-right"></i> 目前尚無資料
                      <i class="fal fa-angle-double-left"></i>
                    </td>
                    <td v-else colspan="4">收入總額 {{allGetCount}} 元</td>
                  </tr>
                </tfoot>
              </table>
            </div>
            <div class="row justify-content-center">
              <div class="col-md-5">
                <div class="back">
                  <span @click="goBack">返回功能選項</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 1s ease;
  .current-month-title {
    display: block;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    padding: 20px 0 20px 0;
    color: white;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .current-month-get-outer-frame {
    overflow: hidden;
    .current-month-get-outer {
      margin-top: -700px;
      transition: 1s ease;
      .choose-date-outer {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin: 10px 0 10px 0;
        overflow: hidden;
        span {
          display: block;
          color: white;
          margin: 0 0 -4px 0;
          font-weight: bold;
          font-size: 18px;
          text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
          cursor: pointer;
          user-select: none;
        }
        .arrow {
          color: white;
          padding: 2px 6.5px 2px 6.5px;
          font-weight: bold;
          font-size: 18px;
          text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
          transform: rotate(0deg);
          transition: 0.7s ease;
        }
        .arrow-active {
          transform: rotate(180deg);
        }
        .choose-date {
          margin-right: -210px;
          transition: 0.7s ease;
          select {
            -webkit-appearance: none;
            padding: 2px 12px 0px 12px;
            outline: none;
            border: none;
            background-color: rgb(255, 255, 255);
            box-shadow: inset 0 0 0 0 rgba(0, 102, 255, 0.7);
            transition: 0.7s ease;
          }
          select:focus {
            box-shadow: inset 0 0 2px 1px rgba(0, 102, 255, 0.7);
          }
          .choose-year {
            border-radius: 20px 0 0 20px;
          }
          .choose-month {
            border-radius: 0 20px 20px 0;
          }
        }
        .choose-date-active {
          margin-right: 0;
        }
      }
      .current-month-get {
        border-radius: 10px;
        box-shadow: inset 0 0 4px 2px rgba(255, 255, 255, 0.7);
        overflow: hidden;
        .table {
          color: white;
          background-color: rgba(0, 0, 0, 0.7);
          margin-bottom: 0;
          table-layout: fixed;
          text-shadow: 0 1px 1px rgba(0, 0, 0, 0.7);
          thead {
            tr {
              box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7);
              th {
                text-align: center;
                border-top: none;
              }
            }
          }
          tbody {
            tr {
              box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7);
              text-align: center;
              transition: 0.7s ease;
              td {
                text-align: center;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            }
          }
          tfoot {
            tr {
              box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7);
              td {
                text-align: right;
              }
            }
          }
          .alert {
            color: red;
            font-weight: bold;
            border-bottom: 1px solid white;
          }
        }
      }
      .back {
        cursor: pointer;
        user-select: none;
        margin: 15px 0 15px 0;
        background-color: rgb(0, 102, 255);
        border-radius: 20px;
        span {
          text-align: center;
          color: white;
          display: block;
          padding: 5px 0 5px 0;
        }
      }
    }
    .current-month-get-outer-active {
      margin-top: 0;
    }
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      showGetGroup: [],
      showGetGroupTemp: [],
      allGetNum: [],
      allGetCount: "",
      monthText: "",
      yearItem: [
        {
          year: 2020,
        },
        {
          year: 2021,
        },
        {
          year: 2022,
        },
        {
          year: 2023,
        },
        {
          year: 2024,
        },
        {
          year: 2025,
        },
      ],
      year: null,
      month: null,
      dateToggle: false,
      pageAnimate: false,
      currentGet: false,
    };
  },
  mounted() {
    setTimeout(() => (this.pageAnimate = true), 100);
    setTimeout(() => (this.currentGet = true), 1100);
  },
  methods: {
    showDate() {
      this.dateToggle == false
        ? (this.dateToggle = true)
        : (this.dateToggle = false);
    },
    goBack() {
      this.currentGet = false;
      setTimeout(() => (this.pageAnimate = false), 1000);
      setTimeout(() => this.$router.push("/main"), 2000);
    },
    currentGetMonth() {
      let year = this.year;
      let month = this.month;
      this.monthText = this.month;
      this.showGetGroupTemp = [];
      this.showGetGroup = [];
      this.allGetNum = [];
      this.allGetCount = "";
      this.getGets(year, month);
      setTimeout(() => (this.dateToggle = false), 500);
    },
    timeObject() {
      const time = new Date();
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      this.year = year;
      this.month = month;
      this.monthText = month;
      this.getGets(year, month);
    },
    getGets(year, month) {
      this.axios
        .get(
          `https://my-cash-20xx.firebaseio.com/cashdb/gets/0/year/${year}/month.json`
        )
        .then((res) => {
          this.showGetGroupTemp = Object.keys(res.data).map(
            (key) => res.data[key]
          );
          this.showGetGroupTemp = this.showGetGroupTemp.sort(
            (max, min) => max.getDate - min.getDate
          );
          this.showGetGroupTemp.forEach((key) =>
            key.getMonth == month ? this.showGetGroup.push(key) : null
          );
          this.showGetGroup.forEach((key) =>
            this.allGetNum.push(Number(key.getCash))
          );
          this.allGetCount = this.allGetNum.reduce((front, end) => front + end);
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.timeObject();
  },
};
</script>