<template>
  <div class="page-animate">
    <span class="system-title">收支管理選項</span>
    <div class="row justify-content-center">
      <div class="col-md-10">
        <div class="select-box-outer">
          <div class="row no-gutters row-hide-frame" :class="{'row-hide':openStatus}">
            <div class="col-md-3" v-for="(itemName,index) in selectItem" :key="index">
              <div class="select-box" @click="goFunction">
                <span :data-num="index">{{itemName.selectName}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.page-animate {
  opacity: 0;
  transition: 1s ease;
  .system-title {
    display: block;
    text-align: center;
    padding: 20px 0 20px 0;
    font-size: 30px;
    color: white;
    font-weight: bold;
    text-shadow: 0 2px 1px rgba(0, 0, 0, 0.7);
  }
  .select-box-outer {
    overflow: hidden;
    .row-hide-frame {
      margin-top: -500px;
      transition: 1s ease;
      .select-box {
        color: white;
        cursor: pointer;
        user-select: none;
        background-color: rgba(0, 0, 0, 0.7);
        transition: 1s ease;
        margin: 5px;
        box-shadow: inset 0 0 2px 1px rgba(255, 255, 255, 0.7),
          0 0 2px 1px rgba(0, 0, 0, 0.7);
        border-radius: 10px;
        span {
          display: block;
          text-align: center;
          font-size: 20px;
          font-weight: bold;
          padding: 100px 0 100px 0;
        }
        @media screen and (max-width: 768px) {
          span {
            padding: 50px 0 50px 0;
          }
        }
        @media screen and (max-width: 414px) {
          span {
            padding: 20px 0 20px 0;
          }
        }
      }
      .select-box-active {
        color: black;
        background-color: rgba(255, 255, 255, 0.7);
        box-shadow: inset 0 0 2px 1px rgba(0, 0, 0, 0.7),
          0 0 2px 1px rgba(0, 0, 0, 0.7);
      }
    }
    @media screen and (max-width: 414px) {
      .row-hide-frame {
        margin-top: -1450px;
      }
    }
    .row-hide {
      margin-top: 0;
    }
  }
}
.page-animate-active {
  opacity: 1;
}
</style>
<script>
export default {
  data() {
    return {
      openStatus: false,
      selectItem: [
        { selectNum: 0, selectName: "新增支出", selectUrl: "add_cost" },
        { selectNum: 1, selectName: "新增收入", selectUrl: "add_get" },
        {
          selectNum: 2,
          selectName: "當月支出",
          selectUrl: "current_cost_month",
        },
        {
          selectNum: 3,
          selectName: "每月支出",
          selectUrl: "every_cost_month",
        },
        {
          selectNum: 4,
          selectName: "當月收入",
          selectUrl: "current_get_month",
        },
        {
          selectNum: 5,
          selectName: "每月收入",
          selectUrl: "every_get_month",
        },

        {
          selectNum: 6,
          selectName: "剩餘總額",
          selectUrl: "last_total",
        },
        {
          selectNum: 7,
          selectName: "支出試算",
          selectUrl: "cost_test",
        },
      ],
    };
  },
  mounted() {
    setTimeout(() => {
      document
        .querySelector(".page-animate")
        .classList.add("page-animate-active");
      setTimeout(() => (this.openStatus = true), 1000);
    }, 300);
  },
  methods: {
    goFunction(e) {
      this.selectItem.forEach((key) => {
        if (key.selectNum == Number(e.target.dataset.num)) {
          [...document.querySelectorAll(".select-box")][
            key.selectNum
          ].classList.add("select-box-active");
          setTimeout(() => (this.openStatus = false), 590);
          setTimeout(
            () =>
              document
                .querySelector(".page-animate")
                .classList.remove("page-animate-active"),
            1590
          );
          setTimeout(() => this.$router.push(`/${key.selectUrl}`), 2590);
        }
      });
    },
  },
};
</script>
